.header{
  background: transparent;
  position: relative;
  padding: 0 120px;
  &__sticky{
    background: black;
    position: fixed;
    //width: 100%;
    left: 0;
    right: 0;
    top: 0;
    z-index: 20;
    padding: 0 120px;
  }
  &__logo{
    display: flex;
    align-items: center;
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 0;
  }
}
.nav{
  &__menu{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
    & li{
      margin: 0 15px;
      a{
        text-decoration: none;
        font-weight: 500;
        font-size: $text;
        &:hover{
          cursor: pointer;
        }
      }
    }

  }
}