.btn{
  border-radius: $borderRadius;
  padding: 0px 64px;
  gap: 8px;
  //text style
  font-weight: 600;
  font-size: $button;

  //svg icon
  svg{
    margin: 8px 0;
  }

  &:disabled{
    cursor: not-allowed;
  }
}

.primary{
  background: $primary-50;
  border: 1px solid $primary-50;
  //text style
  color: $text-90;
  @extend .btn;
  &:hover{
    background: $primary-40;
    border-color: $primary-40;
    cursor: pointer;
  }

  &:active{
    background: $primary-30;
    border-color: $primary-30;
  }

  &:disabled{
    background: $primary-80;
    border-color: $primary-80;
  }
}

.secondary{
  background:  $surface-60;
  border: 1px solid $surface-40;
  //text style
  color: $text-90;
  @extend .btn;

  &:hover{
    background: $surface-80-Background;
    border-color: $surface-80-Background;
    cursor: pointer;
  }

  &:active{
    background: $surface-60;
    border-color: $surface-60;
  }

  &:disabled{
    background: $surface-80-Divider;
    border-color: $surface-80-Divider;

    //text-style
    color: $text-40;
  }
}

.warning{
  background:  $error-30  ;
  border: 1px solid $error-30;
  //text style
  color: $error-50;
  @extend .btn;

  &:hover{
    background: $error-20;
    border-color: $error-20;
    cursor: pointer;
  }

  &:active{
    background: $error-40;
    border-color: $error-40;
  }

  &:disabled{
    background: $error-30;
    border-color: $error-30;

    //text-style
    color: $error-40;
  }
}

.button{
  &__primary{
    @extend .primary;
    height: 56px;
  }

  &__medium__primary{
    @extend .primary;
    height: 48px;
  }

  &__secondary{
    @extend .secondary;
    height: 56px;
  }
  
  &__medium__secondary{
    @extend .secondary;
    height: 48px;
  }

  &__warning{
    @extend .warning;
    height: 56px;
  }

  &__medium__warning{
    @extend .warning;
    height: 48px;
  }
}

.icon{
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  //text style
  color : $text-50;
}
.icon{
  &__filled{
    background: $surface-80-Divider;
    color: $surface-80-Divider;
    @extend .icon;
    &:hover{
      background: $surface-70;
      color: $surface-70;
    }

    &:active{
      background: $surface-60;
      color: $surface-60;
    }

    &:disabled{
      background: $surface-70;
      color: $surface-70;
    }
  }

  &__empty{
    @extend .icon;
    &:hover{
      background: $surface-70;
      color: $surface-70;
    }

    &:active{
      background: $surface-60;
      color: $surface-60;
    }

    &:disabled{
      background: $surface-70;
      color: $surface-70;
    }
  }
}