html,
body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Poppins !important;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
span,
ul,
li,
th,
td,
div,
code,
button,
label,
input {
  font-family: Poppins !important;
}
