.footer{
  background: #000000;
  &__info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 69px 0;

    &__social_link{
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 32px 0;
      a{
        margin: 0 12px;
        &:hover{
          cursor: pointer;
        }
      }
    }
  }

  &__bottom{
    border-top: 0.5px solid #3A3E46;
    padding: 35px 0;
    @media screen and (max-width: 576px) {
      padding: 15px 0;
    }
      ul{
      display: flex;
      align-items: center;
      list-style-type: none;
      margin: 0;
      padding: 0;
      @media screen and (max-width: 576px) {
        width: 100%;
        justify-content: space-between;
        margin-bottom: 15px;
      }
        li a{
        font-size: 14px;
        color: #ECEDEE;
        margin-right: 40px;
        @media screen and (max-width: 576px) {
          font-size: 8px;
          margin-right: unset;
        }
        }
    }
    .copy-right{
      @media screen and (max-width: 576px) {
        text-align: center;
      }
      }
  }
}